header.main{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 115px;
  background: var(--color-transparent);
  z-index: 1000;
  &.bg{
    background: var(--color-white);
  }
  &.hidden{
    top: -130px;
  }
  .wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;
    a.logo{
      display: block;
      width: 160px;
      height: 55px;
      img{
        display: block;
        width: 100%;
      }
    }
    ul{
      display: flex;
      align-items: center;
      &.nav{
        column-gap: 30px;
        li{
          position: relative;
          &.sub{
            &:hover{
              &:before{
                opacity: 1;
                visibility: visible;
              }
              ul{
                opacity: 1;
                visibility: visible;
              }
            }
            &:before{
              position: absolute;
              top: -8px;
              left: -16px;
              display: block;
              width: calc(100% + 32px);
              height: calc(100% + 16px);
              border-radius: 10px 10px 0 0;
              background: var(--color-white);
              opacity: 0;
              visibility: hidden;
              content: '';
            }
            a{
              position: relative;
              display: flex;
              align-items: center;
              *{
                z-index: 2;
              }
              &:hover{
                &:after{
                  transform: scale(-1);
                }
              }
              &:after{
                position: relative;
                display: block;
                width: 10px;
                height: 6px;
                margin: 0 0 0 8px;
                background: url("data:image/svg+xml,%3Csvg width='10' height='6' viewBox='0 0 10 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9 0.584961L5 4.58496L1 0.584961' stroke='%23121720'/%3E%3C/svg%3E%0A") no-repeat;
                content: '';
              }
            }
          }
          a{
            font-size: 16px;
            white-space: nowrap;
          }
          ul{
            position: absolute;
            top: calc(100% + 8px);
            left: -16px;
            display: block;
            padding: 8px 16px;
            border-radius: 0 10px 10px 10px;
            background: var(--color-white);
            opacity: 0;
            visibility: hidden;
            li{
              padding: 0 0 15px;
              &:last-child{
                padding: 0;
              }
              &:before{
                display: none;
              }
              a{
                font-size: 14px;
                &:after{
                  display: none!important;
                }
              }
            }
          }
        }
      }
      &.info{
        column-gap: 40px;
        li{
          button{
            &.search{
              width: 28px;
              height: 28px;
              &:hover{
                svg{
                  use{
                    stroke: var(--color-text-hover);
                  }
                }
              }
              svg{
                use{
                  stroke: var(--color-text-main);
                  stroke-width: 1.5;
                }
              }
            }
            &.lang{
              font-size: 14px;
              color: var(--color-text-gray);
              &:hover{
                &:hover{
                  color: var(--color-text-main);
                  &:after{
                    border-top-color: var(--color-text-main);
                  }
                }
              }
              &:after{
                display: block;
                width: 0; 
                height: 0;
                margin: 0 0 0 4px;
                border-left: 3px solid var(--color-transparent);
                border-right: 3px solid var(--color-transparent);
                border-top: 4px solid var(--color-text-gray);
                content: "";
              }
            }
          }
        }
      }
    }
  }
}